import React, { useEffect } from 'react';

function moveSlowly(deltaY: number, offsetTop: number, element: HTMLElement) {
  const maxRange = offsetTop + 10,
    minRange = offsetTop - 10;
  if (element.style.top === '') element.style.top = `${offsetTop}vh`;
  if (
    (parseInt(element.style.top) > maxRange && deltaY > 0) ||
    (parseInt(element.style.top) < minRange && deltaY < 0)
  )
    return;
  if (deltaY > 0) element.style.top = `${parseInt(element.style.top) + 1}vh`;
  else element.style.top = `${parseInt(element.style.top) - 1}vh`;
}

const Disconnected = () => {
  useEffect(() => {
    function handleWheel(e: WheelEvent) {
      const title = document.getElementById('title');
      const label = document.getElementById('label');
      if (label) moveSlowly(e.deltaY, 0, label);
      if (title) moveSlowly(e.deltaY, 50, title);
    }
    const container = document.getElementById('container');
    if (container) {
      container.addEventListener('wheel', handleWheel);
      return () => {
        container.removeEventListener('wheel', handleWheel);
      };
    }
  }, []);

  return (
    <div id='container' className='flex-container'>
      <div className='flex-item-1'>
        <img
          className='images image-2 first-left-image'
          src='https://www.dropbox.com/s/povfq4oj9p7nxaj/Disconnected%2001.jpg?dl=1'
          alt='Disconnected-01'
          width={800}
          height={600}
        />
        <img
          className='images image-3'
          src='https://www.dropbox.com/s/urwganonfdye1k9/Disconnected%2002.jpg?dl=1'
          alt='Disconnected-02'
          width={600}
          height={800}
        />
        <video
          autoPlay
          loop
          className='images image-1'
          width={800}
          height={600}
        >
          <source
            src='https://www.dropbox.com/s/nmwost8czpsc4gb/Disconnected%2004.mp4?dl=1'
            type='video/mp4'
          />
        </video>
        <div className='image-box image-2 '>
          <img
            className='images'
            src='https://www.dropbox.com/s/ibbm3njtwqppjjh/Disconnected%2006.png?dl=1'
            alt='Disconnected-06'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/ruuxo3viwsytu8e/Disconnected%2006_hover.png?dl=1'
            alt='Disconnected-06'
            title='Disconnected-06'
            width={600}
            height={800}
          />
        </div>
      </div>
      <div className='flex-item-2'>
        <p id='label' className='label'>
          &emsp;Many of us are restricted to see out loved ones. The feelings of
          isolation hit us in more places than the heart and head. Even though
          technology enables us to connect with out family, friends and
          colleagues regardless of where we are, but we can easily feel isolated
          and disconnected without physical contract.
        </p>
        <p id='title' className='title'>
          Disconnected
        </p>
        <div className='image-box image-2 first-right-image'>
          <img
            className='images hover'
            src='https://www.dropbox.com/s/280r1i57jpjvzpk/Disconnected%2003_hover.jpg?dl=1'
            alt='Disconnceted-03'
            width={800}
            height={600}
          />
          <img
            className='images'
            src='https://www.dropbox.com/s/bkyob9k4jdd283z/Disconnected%2003.png?dl=1'
            alt='Disconnected-03'
            width={800}
            height={600}
          />
        </div>
        <div className='image-box image-3'>
          <img
            className='images'
            src='https://www.dropbox.com/s/52zh5nns0n9yx0w/Disconnected%2005.jpg?dl=1'
            alt='Disconnected-05'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/3b73qtc8pgca91g/Disconnected%2005_hover.jpg?dl=1'
            alt='Disconnected-05'
            width={600}
            height={800}
          />
        </div>
        <div className='image-box image-1'>
          <img
            className='images'
            src='https://www.dropbox.com/s/g8xie6elnxp7aao/Disconnected%2007.png?dl=1'
            alt='Disconnected-07'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/mjn4uqk6pobcmrl/Disconnected%2007_hover.jpg?dl=1'
            alt='Disconnected-07'
            width={600}
            height={800}
          />
        </div>
      </div>
    </div>
  );
};

export { Disconnected };
